@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

body { font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 400; font-size: 14px; line-height: 1.4; min-width: 320px; }

.l-center { max-width: 1070px; margin: 0 auto; }

.row { font-size: 0; }

.col-s { display: inline-block; width: 33.33%; padding: 0 15px; }

strong, b { font-weight: 400; }

i, em { font-style: normal; }

h1, .h1 { font-size: 60px; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 700; }

.h2 { font-size: 45px; color: #0e0e0e; line-height: 1.5; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 300; }

a { color: #000; text-decoration: none; }

.container { max-width: 1320px; padding: 0 15px; margin: 0 auto; }
@media only screen and (max-width: 1320px) { .container { max-width: 990px; } }
@media only screen and (max-width: 990px) { .container { max-width: 660px; } }
@media only screen and (max-width: 660px) { .container { width: 100%; } }

.tech-row { padding: 10px 0; }

.btn { display: inline-block; text-transform: uppercase; color: #282828; position: relative; padding: 7px 18px 6px; font-size: 11px; transition: color 0.3s; z-index: 1; }
.btn:before { content: ""; position: absolute; left: 0; top: -1px; bottom: -1px; width: 5px; background: #333; transition: width 0.3s; z-index: -1; }
.btn:hover { color: #fff; }
.btn:hover:before { width: 100%; }
.btn_gold:before { background-color: #f2c05e; }
.btn_orange:before { background-color: #ff8c61; }
.btn_red:before { background-color: #d27857; }
.btn_violet:before { background-color: #c0a6cd; }

.item { position: relative; margin-bottom: 30px; border: 1px solid #c0a6cd; }
.item_gold { border-color: #f2c05e; }
.item_gold .item__link { color: #f2c05e; }
.item_violet { border-color: #ff8c61; }
.item_mod .item__txt { font-size: 22px; font-family: "Lora", serif; font-weight: 400; }
.item__img { position: relative; display: block; height: 300px; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }
.item__img:before { display: none; content: ""; position: absolute; top: 0; left: 0; right: 0; bottom: 0; }
.item__txt { padding: 17px 30px; height: 160px; text-align: center; font-size: 28px; background: #FFF; color: #282828; overflow: hidden; line-height: 28px; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 500; }
.item__author { font-size: 14px; color: #909090; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 300; }
.item__link { display: inline-block; text-decoration: underline; font-size: 14px; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 400; font-style: italic; }
.item__link:hover { text-decoration: none; }
.item__title { position: absolute; bottom: 10px; left: 0; right: 0; text-align: center; }
.item__name { font-size: 28px; color: #FFF; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 700; }
.item .btn { position: absolute; top: 10px; left: -5px; background: #FFF; }

.search { position: relative; display: block; margin: 5px 0; }
.search input { display: block; width: 100%; padding: 18.5px 18px 18.5px 108px; font-size: 42px; background: #f4f5f1; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 700; }
.search__btn { position: absolute; top: 50%; left: 17px; margin-top: -30.5px; font-size: 61px; background: none; color: #9bc1bc; line-height: 1; cursor: pointer; }

.filter__bl { padding: 20px 0; font-size: 16px; border-bottom: 1px solid #e4eae9; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 300; *zoom: 1; }
.filter__bl:after { content: " "; display: table; clear: both; }
.filter__el { margin-top: 5px; *zoom: 1; }
.filter__el:after { content: " "; display: table; clear: both; }
.filter__name { float: left; min-width: 88px; margin-right: 20px; color: #282828; }
.filter__info { font-size: 0; overflow: hidden; }
.filter__info p { font-size: 16px; }
.filter__tag { display: inline-block; margin-right: 10px; text-transform: uppercase; font-size: 16px; color: #9bc1bc; letter-spacing: 1.2px; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 700; }
.filter__tag:hover { text-decoration: underline; }
.filter__num { white-space: nowrap; }
.filter .sort { float: right; margin-left: 20px; }
@media only screen and (max-width: 990px) { .filter .sort { float: none; margin: 0 0 20px 0; } }
@media only screen and (max-width: 480px) { .filter .sort__tag, .filter .sort__bl { display: block; }
  .filter .sort__tag { margin-bottom: 10px; } }

.sort { font-size: 0; }
.sort__tag, .sort__bl { display: inline-block; vertical-align: middle; }
.sort__tag { margin-right: 10px; font-size: 16px; }
.sort__bl { margin: 0 -5px; font-size: 0; }
.sort__tab { display: inline-block; margin: 0 5px; padding: 7px 15px; font-size: 14px; vertical-align: middle; font-family: "Roboto", Arial, sans-serif; }
.sort__tab.is-active { background: #9bc1bc; color: #FFF; }

.textbl { display: block; padding: 21px 0; border-bottom: 1px solid #e4eae9; *zoom: 1; }
.textbl:after { content: " "; display: table; clear: both; }
.textbl:hover .textbl__ttl { text-decoration: underline; }
.textbl_no-border { border-color: transparent; }
.textbl__inner { max-width: 80%; }
.textbl__ttl { margin-bottom: 7px; font-size: 21px; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 700; }
.textbl__ttl span { color: #9bc1bc; }
.textbl__txt { font-size: 18px; color: #353535; line-height: 1.5; font-family: "Lora", serif; font-weight: 400; }
.textbl__img { float: right; width: 190px; margin: 15px 0; }
.textbl__img img { width: 100%; }
.textbl__tags { font-size: 0; margin-top: 20px; }
.textbl__name { margin-right: 20px; text-transform: uppercase; font-size: 14px; }
.textbl__name.textbl_violet { color: #ff8c61; }
.textbl__name.textbl_red { color: #d27857; }
.textbl__name.textbl_mint { color: #9bc1bc; }
.textbl__author { margin-right: 20px; font-size: 14px; color: #293949; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 700; }
.textbl__date { font-size: 14px; color: #AEB0B2; }
.textbl__highlight { color: #9bc1bc; }

.more { padding: 35px 0; background: #e4eae9; }


